@import url("https://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Spartan", sans-serif;
}

html {
  scroll-behavior: smooth;
}

/* Global Styles */
/* Update these styles in your existing CSS or override existing styles */

/* Search input styles */
.search-form input {
  border: none;
  border-radius: 3px;
  padding: 8px; /* Adjust padding for input */
  background-color: #ecf0f1; /* Change the background color */
  color: #333; /* Change the text color */
}

/* Search button styles */
.search-btn {
  font-size: 14px;
  background-color: #fff !important;
  color: #000 !important;
  border-radius: 4px !important;
  cursor: pointer !important;
  border: none !important;
  outline: none !important;
  font-weight: 600 !important;
  transition: 0.2s !important;
  height: 40px;
}

/* Change button color on hover */
.search-btn:hover {
  background-color: #e74c3c !important; /* Change the hover background color */
  color: #fff !important; /* Change the text color on hover */
}

h1 {
  font-size: 50px;
  line-height: 64px;
  color: #222;
}

h2 {
  font-size: 46px;
  line-height: 54px;
  color: #222;
}

h4 {
  font-size: 20px;
  color: #222;
}

h6 {
  font-weight: 700;
  font-size: 12px;
}

p {
  font-size: 16px;
  color: #465b52;
  margin: 15px 0 20px 0;
}

