body {
  background-color: #f8f9fa;
  width: 100%;
}
/* Add these styles to your existing CSS or update the existing styles */

/* Update these styles in your existing CSS or override existing styles */

.navbar {
  align-items: center;
  padding: 20px 80px;
  background: #e3e6f3 !important;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06) !important;
  position: sticky;
  z-index: 999;
  top: 0;
  left: 0;
}

.navbar-brand {
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  color: #1a1a1a !important;
  transform: 0.3s ease;
}

.nav-link {
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  color: #1a1a1a !important;
  transform: 0.3s ease;
}

.navbar-toggler-icon {
  background-color: #ecf0f1; /* Change the toggler icon color */
}

.navbar-toggler {
  border: none;
}

.nav-item {
  margin-right: 15px;
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  color: #1a1a1a !important;
  transform: 0.3s ease;
}

.active {
  border-bottom: 2px solid #ecf0f1; /* Change the active link underline color */
}

.dropdown-menu {
  background-color: #2c3e50; /* Change the dropdown background color */
}

.dropdown-item {
  color: #ecf0f1 !important;
}

.dropdown-item:hover {
  background-color: #2c3e50; /* Change the hover background color */
}

/* Search form styles */
.search-form {
  margin-right: 10px;
  margin-top: 8px;
}

.search-form input {
  border: none;
  border-radius: 3px;
  padding: 8px; /* Adjust padding for input */
  background-color: #ecf0f1; /* Change the background color */
}

.search-form button {
  background-color: #e74c3c; /* Change the button background color */
  border: none;
  border-radius: 3px;
  color: white;
  padding: 8px 12px; /* Adjust padding for button */
  cursor: pointer;
}

/* Badge in the cart link styles */
.navbar-nav .badge {
  background-color: #e74c3c;
  color: #fff;
  font-weight: bold;
  padding: 6px 8px;
  border-radius: 50%;
  margin-left: 5px;
}

/* =============================
==========footer============= */

/* Add this to your existing CSS or update the existing styles */

/* Add this to your existing CSS or update the existing styles */

footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

footer .col {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 20px;
}
footer .logo {
  margin-bottom: 30px;
}
footer h4 {
  font-size: 14px;
  padding-bottom: 20px;
}
footer p {
  font-size: 13px;
  margin: 0 0 8px 0;
}
footer a {
  font-size: 13px;
  color: #222;
  text-decoration: none;
  margin-bottom: 10px;
}

footer .follow i {
  color: #465b52;
  padding-right: 4px;
  cursor: pointer;
}

footer .install .row img {
  border: 1px solid #088178;
  border-radius: 6px;
  height: 45px;
  width: 150px;
}

footer .install img {
  margin: 10px 0 15px 0;
}
footer .follow i:hover,
footer a:hover {
  color: #088178;
}


/* =================================
======== page not found css ======= */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}
/* ========================================= */
/* =========contact us ========= */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ==================== */
.product-link {
  text-decoration: none !important;
  color: black !important;
}

.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}
.cat-btn:hover {
  background-color: #434343;
  color: white;
}
/* ================
====dashboard
=================== */

.dashboard {
  margin-top: 100px !important;
  margin-right: 10px !important;
}

.dashboard-menu h4 {
  background-color: #434343 !important;
  color: white;
  padding: 20px 0px;
}



