@media (max-width: 477px) {
  .section-p1 {
    padding: 20px;
  }
  .header {
    padding: 10px 30px;
  }
  h1 {
    font-size: 38px;
  }
  h2 {
    font-size: 32px;
  }

  .hero {
    padding: 0 20px;
    background-position: 55%;
  }
  .feature {
    justify-content: space-between;
  }
  .feature .feature-box {
    width: 155px;
    margin: 0 0 15px 0;
  }
  .product .pro {
    width: 100%;
  }

  .banner {
    height: 40vh;
  }
  .sm-banner .banner-box {
