  }

  .newsletter {
    padding: 40px 20px;
  }
  .newsletter .form {
    width: 100%;
  }

  /* single-product */

  .prodetails {
    display: flex;
    flex-direction: column;
  }
  .prodetails .single-pro-image {
    width: 100%;
    margin-right: 0px;
  }
  .prodetails .single-pro-details {
    width: 100%;
  }

  /* blog page */

  .blog {
    padding: 100px 20px 0 20px;
  }

  .blog .blog-box {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .blog .blog-img {
    width: 100%;
    margin-right: 0px;
    margin-bottom: 30px;
  }
  .blog .blog-details {
    width: 100%;
  }

  /* about page */
  .about-head {
    flex-direction: column;
  }
  .about-head img {
    width: 100%;
    margin-bottom: 20px;
  }
  .about-head div {
    padding-left: 0px;
  }
  .about-app video {
    width: 100%;
  }
  /* Contact */
  .contact-details {
    flex-direction: column;
  }
  .contact-details .details {
    width: 100%;
    margin-bottom: 30px;
  }
  .contact-details .map {
    width: 100%;
  }
  .form-details {
    margin: 10px;
    padding: 30px 10px;
    flex-wrap: wrap;
  }
  .form-details form {
    width: 100%;
    margin-bottom: 30px;
  }
  .cart-add{
    flex-direction: column;
  }
  .cart-add .coupon {
    width: 100%;
}
.subtotal{
  width: 100%;
padding: 20px;
}

