.banner-img {
  margin-top: 60px;
}

.section-p1 {
  padding: 40px 80px;
}

/* home page */
.hero {
  background-image: url('./hero4.png');
  width: 100%;
  height: 90vh;
  /* Center and scale the image nicely */
  background-size: cover;
  background-position: top 25% right 0;
  padding: 0 80px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.hero h4 {
  padding-bottom: 15px;
}
.hero h1 {
  color: #088178;
}

.hero button {
  background-image: url(./button.png);
  background-color: transparent;
  color: #088178;
  border: 0;
  padding: 14px 80px 14px 65px;
  background-repeat: no-repeat;
  font-size: 15px;
  font-weight: 700;
  cursor: pointer;
}



.search-btn {
  font-size: 14px;
  background-color: #e8a4a4 !important;
  color: #000 !important;
  border-radius: 4px !important;
  cursor: pointer !important;
  border: none !important;
  outline: none !important;
  font-weight: 600 !important;
  transition: 0.2s !important;
  height: 40px;
}

/* Change button color on hover */
.search-btn:hover {
  background-color: #e74c3c !important; /* Change the hover background color */
  color: #fff !important; /* Change the text color on hover */
}





.product {
  text-align: center;
}

.product .pro-container {
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  flex-wrap: wrap;
}
.product .pro {
  width: calc(33.33% - 20px); 
    height: 80%;

  min-width: 250px;
  padding: 10px 20px;
  border: 1px solid #cce7d0;
  border-radius: 20px;
  cursor: pointer;
  box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.02);
  margin: 15px 0;
  transition: 0.2 ease;
  position: relative;
}
.product .pro:hover {
  box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.06);
}
.product .pro img {
  width: 100%;
  border-radius: 20px;
}

.product .pro .description {
  text-align: start;
  padding: 10px 0;
}
.product .pro span {
  font-size: 12px;
  color: #606063;
}
.product .pro .description h5 {
  padding-top: 7px;
  color: #1a1a1a;
  font-size: 14px;
}

.product .pro .description h5 {
  font-size: 15px;
  font-weight: 700;
  color: #088178;
}
.product .pro .more{
  overflow: hidden;
  background-color: #9f81db;
  font-weight: 500;
  color: #e0eceb;
  border: 1px solid #cce7d0;
  width: 150px;
}
.product .pro .cart {
  overflow: hidden;
  background-color: #e8f6ea;
  font-weight: 500;
  color: #088178;
  border: 1px solid #cce7d0;
  width: 150px;

}









.feature {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;

}
.feature .feature-box {
  width: 180px;
  text-align: center;
  padding: 25px 15px;
  box-shadow: 20px 20px 34px rgba(0, 0, 0, 0.06);
  border: 1px solid #cce7d0;
  border-radius: 4px;
  margin: 15px 0;
}

.feature .feature-box:hover {
  box-shadow: 10px 10px 54px rgba(70, 62, 221, 0.1);
}

.feature .feature-box img {
  width: 100%;
  margin-bottom: 10px;
}
.feature .feature-box h6 {
  padding: 9px 8px 6px 8px;
  line-height: 1;
  border-radius: 4px;
  color: #088178;
  background-color: #fddde4;
}

.feature .feature-box:nth-child(2) h6 {
  background-color: #cdebbc;
}
.feature .feature-box:nth-child(3) h6 {
  background-color: #d1e8f2;
}
.feature .feature-box:nth-child(4) h6 {
  background-color: #cdd4f8;
}
.feature .feature-box:nth-child(5) h6 {
  background-color: #f6dbf6;
}
.feature .feature-box:nth-child(6) h6 {
  background-color: #fff2e5;
}











.home-page h1 {
  color: gray;
  font-family: "Playfair Display", serif;
}

.home-page {
  display: flex;
  flex-wrap: wrap;
}
.home-page .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 18rem;
}
.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.home-page .card-price {
  color: green;
  font-weight: bold;
}
.home-page button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}

.home-page .card img:hover {
  transform: scale(0.9);
}

.home-page .card-img-top {
  height: 300px !important;
}
