/* AuthStyles.css */

.form-container {
  background-color: #f8f9fa;
  padding: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.auth-form {
  width: 400px;
  padding: 30px;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.auth-title {
  text-align: center;
  margin-bottom: 20px;
  font-weight: bold;
  color: #333;
  font-size: 28px;
}

.form-group {
  margin-bottom: 20px;
}

.form-control {
  width: 100%;
  padding: 15px;
  margin-bottom: 20px;
  border: 1px solid #ced4da;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s ease-in-out;
}

.form-control:focus {
  border-color: #007bff;
}

.forgot-btn {
  color: #007bff;
  background-color: transparent;
  border: none;
  text-decoration: underline;
  cursor: pointer;
  font-size: 14px;
  transition: color 0.3s ease-in-out;
}

.forgot-btn:hover {
  color: #0056b3;
}

.btn-primary {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 15px;
  border-radius: 8px;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.btn-primary:hover {
  background-color: #0056b3;
}
