.prodetails {
  display: flex;
  margin-top: 20px;
}

.prodetails .single-pro-image {
  width: 40%;
  margin-right: 50px;
}
.small-img-group {
  display: flex;
  justify-content: space-between;
}

.small-img-col {
  flex-basis: 24%;
  cursor: pointer;
}

.add-btn{
  font-size: 14px;
  background-color: #5d9d68 !important;
  color: white !important;
  border-radius: 4px !important;
  cursor: pointer !important;
  border: none !important;
  outline: none !important;
  font-weight: 600 !important;
  transition: 0.2s !important;
  height: 40px;
width: 150px;
}

.add-btn:hover {
  background-color: #e74c3c !important; /* Change the hover background color */
  color: #fff !important; /* Change the text color on hover */
}

.prodetails .single-pro-details {
  width: 50%;
  padding-top: 30px;
}

.prodetails .single-pro-details h4 {
  padding: 40px 0 20px 0;
}
.prodetails .single-pro-details h2 {
  font-size: 26px;
}
.prodetails .single-pro-details select {
  display: block;
  padding: 5px 10px;
  margin-bottom: 10px;
}

.prodetails .single-pro-details input {
  width: 50px;
  height: 47px;
  padding-left: 10px;
  font-size: 16px;
  margin-right: 10px;
}

.prodetails .single-pro-details button {
  background: #088178;
  color: #fff;
}

.prodetails .single-pro-details input:focus {
  outline: none;
}

.prodetails .single-pro-details span {
  line-height: 25px;
}
  .similar-products .card {
    background-color: rgba(128, 128, 128, 0.097);
    width: 18rem;
  }
  
  .similar-products .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .similar-products .card-price {
    color: green;
    font-weight: bold;
  }
  .similar-products button {
    border-radius: 0;
    width: 100%;
    border-top-left-radius: 5px;
    border-bottom-right-radius: 5px;
    font-size: 14px;
    font-weight: bold;
  }
  
  .similar-products .card-text {
    color: rgb(90, 89, 89);
  }
  
  .similar-products .ok img:hover {
    transform: scale(0.9);
  }
  
  .similar-products .card-img-top {
    height: 300px !important;
  }


  
.product {
  text-align: center;
}

.product .pro-container {
  display: flex;
  justify-content: space-between;
  padding-top: 20px;
  flex-wrap: wrap;
}
.product .pro {
  width: calc(33.33% - 20px); 
    height: 80%;

  min-width: 250px;
  padding: 10px 20px;
  border: 1px solid #cce7d0;
  border-radius: 20px;
  cursor: pointer;
  box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.02);
  margin: 15px 0;
  transition: 0.2 ease;
  position: relative;
}
.product .pro:hover {
  box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.06);
}
.product .pro img {
  width: 100%;
  border-radius: 20px;
}

.product .pro .description {
  text-align: start;
  padding: 10px 0;
}
.product .pro span {
  font-size: 12px;
  color: #606063;
}
.product .pro .description h5 {
  padding-top: 7px;
  color: #1a1a1a;
  font-size: 14px;
}

.product .pro .description h5 {
  font-size: 15px;
  font-weight: 700;
  color: #088178;
}
.product .pro .more{
  overflow: hidden;
  background-color: #9f81db;
  font-weight: 500;
  color: #e0eceb;
  border: 1px solid #cce7d0;
  width: 150px;
}
.product .pro .cart {
  overflow: hidden;
  background-color: #e8f6ea;
  font-weight: 500;
  color: #088178;
  border: 1px solid #cce7d0;
  width: 150px;

}

