  .product {
    text-align: center;
  }
  
  .product .pro-container {
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    flex-wrap: wrap;
  }
  .product .pro {
    width: calc(33.33% - 20px); 
      height: 80%;
  
    min-width: 250px;
    padding: 10px 20px;
    border: 1px solid #cce7d0;
    border-radius: 20px;
    cursor: pointer;
    box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.02);
    margin: 15px 0;
    transition: 0.2 ease;
    position: relative;
  }
  .product .pro:hover {
    box-shadow: 20px 20px 30px rgba(0, 0, 0, 0.06);
  }
  .product .pro img {
    width: 100%;
    border-radius: 20px;
  }
  
  .product .pro .description {
    text-align: start;
    padding: 10px 0;
  }
  .product .pro span {
    font-size: 12px;
    color: #606063;
  }
  .product .pro .description h5 {
    padding-top: 7px;
    color: #1a1a1a;
    font-size: 14px;
  }
  
  .product .pro .description h5 {
    font-size: 15px;
    font-weight: 700;
    color: #088178;
  }
  .product .pro .more{
    overflow: hidden;
    background-color: #9f81db;
    font-weight: 500;
    color: #e0eceb;
    border: 1px solid #cce7d0;
    width: 150px;
  }
  .product .pro .cart {
    overflow: hidden;
    background-color: #e8f6ea;
    font-weight: 500;
    color: #088178;
    border: 1px solid #cce7d0;
    width: 150px;
  
  }
  